.home-section .start {
  z-index: 2;
  color: #fff;
  font-size: 2rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  padding: 2rem 4rem;
  /* border-bottom: 1px solid #fff; */
  cursor: pointer;
  /* transition: all 2s; */
}

.title {
  text-align: center;
  letter-spacing: 10px;
}

.titleWithEffect {
  text-align: center;
  letter-spacing: 10px;
  animation: fadeInDown 1.5s ease-in-out;
}

@keyframes fadeInDown {
  from {
    display: none;
    opacity: 0;
    transform: translateY(300px);
  }
  to {
    opacity: 1;
  }
}
